  import { Card, CardBody, CardHeader } from "react-bootstrap";
  import React from "react";
  import { baseUrl, reArrangeSeries } from "../../../../../utils";
  const AssetsTypeLineChart = React.lazy(() =>
    import("../../layout/common/AssetsTypeLineChart")
  );
  const ContentAnalytics = (props) => {
    const contentAnalytics =
      props.contentAnalytics &&
      Object.keys(props.contentAnalytics).length &&
      reArrangeSeries(props.contentAnalytics,1);

    return (
      <Card className="sw-card-custom gutter-b p-5 card-shadow mb-5">
        <CardHeader className="p-0 pb-3">
          <h6 class="card-label font-weight-bolder">Content Analytis</h6>
        </CardHeader>
        <CardBody className="px-0 pb-0">
          { props.loader ? (
            <div class="py-1 text-center">
              <img
                src={baseUrl(`dashboard/analytic/content-analytis-2.svg`)}
                width={"100%"}
              />
            </div>
          ) : (
            <AssetsTypeLineChart
              section={1}
              contentAnalyticsData={contentAnalytics}
              type={1}
            />
          )}
        </CardBody>
      </Card>
    );
  };

  export default React.memo(ContentAnalytics);
